.error {
	input[type="text"],
	textarea {
		border-color: $pinkish-red-two;
	}
	label {
		color: $pinkish-red-two;	
	}
}
.fl-row {
	margin-bottom: 20px;
	.row {
		margin-bottom: 0;
	}
}
.form-sumb {
	text-align: center;
}
.form__title {
	font-size: 28px;
	text-align: center;
	margin-bottom: 20px;
}
.login-form {
	max-width: 480px;
	margin: 40px auto 50px;
	padding: 40px;
	background: #f5f6f8;
}

.form-tbl {
	width: 100%;
	th, td {
		vertical-align: middle;
		border-bottom: 1px #dcdddf solid;
		padding: 8px 5px;
		text-align: center;
		&:first-child {
			text-align: left;
		}
	}
	.custom-radio {
		padding: 0;
	}
}

.send-ticket {
	padding: 40px;
	background: #f5f6f8;
	margin-bottom: 100px;
	.send-ticket__title {
		font-size: 28px;
		line-height: normal;
		margin-bottom: 20px;
	}
}

.file-fl-note {
	color: $graymy;
	margin-top: 6px;
}

@media (max-width:767px) {
	.login-form {
		padding: 20px;
	}
}