textarea {
	width: 100%;
	height: 120px;
	box-shadow: 0 5px 8px rgba(0, 36, 71, 0.12);
	background-color: #ffffff;
	padding: 18px;
	outline: 0;
	appearance: none;
	color: $black;
	font-size: 14px;
	font-weight: 400;
	border: 1px #FFF solid;
	resize: none;
	&:focus {
		border-color: $bright-cyan;
	}
	&.adaptive-textarea {
		height: auto;
		padding: 14px 18px;
		display:block;
		overflow: hidden;
	}
}
textarea::-webkit-input-placeholder {font-size: 14px; color: $cool-grey;}
textarea::-moz-placeholder          {font-size: 14px; color: $cool-grey;}/* Firefox 19+ */
textarea:-moz-placeholder           {font-size: 14px; color: $cool-grey;}/* Firefox 18- */
textarea:-ms-input-placeholder      {font-size: 14px; color: $cool-grey;}