.tile-categories__item {
  display: block;
  color: $black;
  position: relative;
  overflow: hidden;
  margin-bottom: 24px;
}
.tile-categories__item-name {
  font-size: 18px;
  font-weight: 600;
  line-height: 90px;
  height: 90px;
  background-color: $blue;
  color: #FFF;
  padding: 0 25px;
  span {
    display: inline-block;
    line-height: normal;
    vertical-align: middle;
  }
}
.tile-categories__item-readmore {
  color: $blue;
  position: absolute;
  left: 25px;
  bottom: 25px;
}
.tile-categories__item-container {
  border: 2px solid #f5f6f8;
  border-top: 0;
}
.tile-categories__item-content {
  font-size: 14px;
  font-weight: 500;
  line-height: 22px;
  padding: 25px 25px 75px;
}
.tile-categories__icon {
  fill: $red;
  position: absolute; 
  z-index: 4;
  right: 18px;
  bottom: 17px;
  &--calendar {
    width: 40px;
    height: 40px;
  }
  &--list {
    width: 38px;
    height: 24px;
  }
  &--notebook {
    width: 33px;
    height: 38px;
  }
  &--airplane {
    width: 38px;
    height: 38px;
  }
}