.blue-menu {
	display: flex;
	height: 48px;
	background-color: #004182;
	align-items: stretch;
	flex-wrap: nowrap;
}
.blue-menu__item {
	width: 100%;
	color: #FFF;
	line-height: 48px;
	font-size: 15px;
	font-weight: bold;
	text-transform: uppercase;
	text-align: center;
	&:hover, &--active {
		background-color: $bright-cyan;
		color: #FFF;
		text-decoration: none;
	}
}
.blue-menu--offset-top {
	margin-top: -24px;
	position: relative;
	z-index: 3;
	margin-bottom: 40px;
}

@media(max-width: 767px) {
	.blue-menu {
		flex-direction: column;
		height: auto;
	}	
}