.ticket {
  padding: 20px 0;
  border-bottom: 1px solid $graymy;
  &:last-child {
    border-bottom: 0;
  }
}
.ticket__number {
  background: $graymy;
  color: #ffffff;
  padding: 2px 5px;
  border-radius: 2px;
  display: inline-block;
  font-size: 13px;
  margin-right: 15px;
  margin-bottom: 10px;
}
.ticket__name {
  margin-right: 15px;
}
.ticket__status {
  color: #ffffff;
  padding: 3px 7px;
  border-radius: 4px;
  font-size: 13px;
  &--pending {background: $dark-green;}
  &--new {background: #779500;}
  &--close {background: $graymy;}
}
.ticket__post-date {
  color: $graymy; 
  font-size: 12px;
  margin-bottom: 10px;
  display: inline-block;
}
.ticket__row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .ticket__row {
    display: block;
  }
  .ticket__name {
    display: block;
    margin-bottom: 10px;
  }
}