.search-form {
  margin-bottom: 20px;
}
.search-field-box {
  position: relative;
  svg {
    width: 25px;
    height: 25px;
    fill: $blue;
  }
  button {
    padding: 0;
    border: 0;
    appearance: none;
    border-radius: 0;
    background: none;
    position: absolute;
    right: 10px;
    top: 10px;
  }
}