.message-list {
  border: 2px solid #f5f6f8;  
  margin-bottom: 30px;
}
.latest-messages {
  margin-bottom: 40px;
}
.message {
  padding: 30px;
  border-bottom: 2px solid #f5f6f8;
  overflow: hidden;
  &:last-child {
    border-bottom: 0;
  }
  &--in-dialog {
    border-bottom: 0;
    padding: 10px 20px;
  }
  &--unread {
    // background: #f3f3f3;
    background: $magnolia;
  }
}
.message__user {
  width: 60px;
  float: left;
  margin-right: 30px;
  .message__autor {
    display: none;
  }
}
.message__user-avatar {
  img {
    border-radius: 50%;
  }
}
.message__online-status {
  font-size: 11px;
  color: $graymy;
  margin-top: 5px;
  position: relative;
  &:before {
    content: "";
    display: inline-block;
    width: 6px;
    height: 6px;
    background: $graymy;
    border-radius: 50%;
    vertical-align: 1px;
    margin-right: 5px;
  }
  &--online {
    color: $green;
    &:before {
      background: $green;
    }
  }
}
.message__meta {
  overflow: hidden;
}
.message__autor {
  color: $graymy;
  margin-bottom: 15px;
} 
.message__text {

}
.message__attachment {
  margin-top: 20px;
}
.message__attachment-title {
  font-weight: 600;
  margin-bottom: 10px;
}
.message__attachment-item {
  margin-bottom: 5px;
}
.message__attachment-icon {
  width: 10px;
  height: 16px;
  vertical-align: -2px;
  margin-right: 10px;
}
.message__attachment-link {

}

@media (max-width: 760px) {
  .message__autor {
    font-size: 14px;
    line-height: 16px;
    overflow: hidden;
    margin-bottom: 5px;
  }
  .message__meta {
    .message__autor {
      display: none;
    }
  } 
  .message__user {
    overflow: hidden;
    width: auto;
    float: none;
    margin-right: 0;
    .message__autor {
      display: block;
    }
  }
  .message__user-avatar {
    float: left;
    margin-right: 20px;
  }
  .message__online-status {
    overflow: hidden;
    margin-top: 0;
  }
  .message__text {
    margin-top: 20px;
  }
  .message {
    padding: 20px;
  }
}