.profile {
  margin-top: 40px;  
}
.profile__photo {
  float: left;
  margin-right: 40px;
  img {
    width: 300px;
    border-radius: 50%;
  }
}
.profile__info {
  overflow: hidden;
}
.profile__name {
  margin-top: 0;
  margin-bottom: 10px;
}
.profile__post {
  color: $graymy;
  font-size: 16px;
}
.profile__contacts {
  margin-top: 15px;
  font-size: 16px;
}
.profile__contacts-item {
  margin-bottom: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 10px;
}

.profile__icon {
  fill: #FFF;
  width: 28px;
  height: 28px;
  vertical-align: middle;
  margin-right: 15px;
  background: $blue;
  padding: 7px;
  border-radius: 14px;
  overflow: visible;
}
.default-photo {
  width: 300px;
  height: 300px;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  svg {
    width: 50%;
    height: 50%;
  }
}
@media (max-width: 767px) {
  .profile__photo {
    width: 200px;
    margin: 0 auto 40px;
    float: none;
    img {
      width: 200px;
    }
  }
  .profile__name {
    text-align: center;
    margin-bottom: 15px;
    line-height: 40px;
  }
  .profile__contacts-item {
    
  } 
  .profile__contacts {
    margin: 30px 0 50px 0;
  }
  .default-photo {
    width: 200px;
    height: 200px;
  }
}