$cyan: #44c9f6;
$graymy: #a0a09f;
$red: #ea1940;
$green: #89d56f;
$dark-green: #6fa85c;
$magnolia: #f9f8fe;
$fontcolor: #3b3a3a;
$black: #000;
$sidebar-opened-width: 275px;
$sidebar-closed-width: 75px;
$sidebar-open-time: .2s;
$popup-border-color: #e1e1e1;

$bluish: #2285bb;
$light-blue-grey: #b7cbe0;
$navy: #002045;
$marine-blue: #002e5b;
$bright-cyan: #44c8f5;
$purple-brown: #652932;
$pinkish-red-two: #ec1646;
$pinkish-red: #ed1847;
$silver-two: #c9c9ca;
$water-blue: #1aaadb;
$silver-three: #dedfe0;
$dusk-blue: #265e95;
$pale-grey: #f5f6f8;
$charcoal-grey: #4d4d4f;
$pinky-red: #ff174b;
$light-navy: #0f5499;
$black: #2e2d2c;
$silver: #e6e7e9;
$darkish-blue: #004282;
$slate-grey: #6e6e71;
$cool-grey: #b4b5b6;
$cement: #989897;

body {
  color: $fontcolor;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
}
a {
  color: $blue;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
h1 {
  color: $black;
  font-size: 32px;
  font-weight: 600;
  line-height: 26px;
  margin: 40px 0 30px 0;
}
h2 {
  color: $black;
  font-size: 22px;
  font-weight: 600;
  line-height: 26px;
}

.wrapper {
  width: 92%;
  margin: 0 auto;
}
.page-container {
  position: relative;
  background: $blue;
}
.sidebar {
  float: left;
  background: $blue;
  width: $sidebar-closed-width;
  min-height: calc(100vh - 125px);
  transition: width $sidebar-open-time;
  overflow: hidden;
  padding-bottom: 40px;
}
.sidebar-content {
  width: $sidebar-opened-width;
}
.sidebar-header {
  overflow: hidden;
  background: #ffffff;
  padding: 17px 28px;
  .current-user {
    margin-top: 0;
  }
  .logout {
    margin-top: 8px;
  }
}
.page-content {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  min-height: calc(100vh - 125px);
}
.header {
  height: 125px;
  background-color: $magnolia;
  display: flex;
  // &--empty {
  //   background: #FFF;
  // }
}
.header-sidebar {
  background: $blue;
  width: $sidebar-closed-width;
  height: 125px;
  border-bottom: 1px solid #295f96;
  transition: width .2s;
  overflow: hidden;
  .sidebar-content {
    display: flex;
    align-items: center;
    height: 125px;
  }
  
}
.sidebar-open {
  width: $sidebar-opened-width;
}
.header-content {
  flex: 1 0;
}
.footer {
  height: 66px;
  line-height: 66px;
  background-color: $magnolia;
}

.read-more {
  color: $blue;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.logo {
  width: 150px;
}

.login-form {
  
  .form-title {
    font-size: 28px;
    margin-bottom: 30px;
    text-align: center;
  }
  input[type="submit"] {
    margin-bottom: 20px;
  }
  
}
.login-form--register {
  max-width: 920px!important;
  width: 100%;
}

.social-login {
  font-size: 0;
  a {
    display: inline-block;
    width: 40px;
    height: 40px;
    background: $blue;
    text-align: center;
    line-height: 40px;
    margin-right: 10px;
    &:hover {
      svg {
        fill: $cyan;
      }
    }
  }
  svg {
    fill: #FFF;
    width: 20px;
    height: 20px;
    vertical-align: middle;
  }
}

.settings-form {
  margin: 80px 0;
  max-width: 400px;
  &--width {
    &--100 {
      max-width: inherit;
    }
  }
  .form-sumb {
    margin-top: 30px;
  }
  input[type="text"], input[type="password"], input[type="number"], input[type="email"] {
    border-color: #e1e1e1;
  }
}
.edit-profile-row {
  display: flex;
  justify-content: center;
}
.edit-profile-col {
  width: 400px;
  
  &--photo {
    width: 300px;
    margin-right: 50px;
  }
}
.load-photo-fl {
  position: relative;
  width: 243px;
  margin: 20px auto 0;
  overflow: hidden;
  input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  label {
    cursor: pointer;
  }
}

.shcedule-preview {
  line-height: 22px;
  p {
    margin: 0;
  }
  b {
    display: inline-block;
    width: 100px;
  }
}

.fl-left {
  float: left;
}

