.friend {
  overflow: hidden;
  padding: 20px 0;
  border-bottom: 1px solid #e2e2e2;
  position: relative;
  &:last-child {
    border-bottom: 0;
  }
  &--status {
    &--online {
      .friend__avatar:after {
        content: "";
        width: 12px;
        height: 12px;
        border: 3px solid #FFF;
        background: $green;
        position: absolute;
        right: 0;
        bottom: 0;
        border-radius: 50%;
      }      
    }
  }
  &--dialog-selector {
    padding: 15px 10px;
    cursor: pointer;
    &:hover {
      background: #f3f3f3;
      .friend__message-time {
        display: none;
      }
      .friend__remove {
        display: block;
      }
    }
  }
  &--dialog-active {
    background: #f3f3f3;
  }
  &--unread {
    .friend__latest-message {
      margin-right: 20px;
    }
    &:after {
      content: "";
      width: 8px;
      height: 8px;
      position: absolute;
      background: $blue;
      border-radius: 50%;
      right: 12px;
      bottom: 22px;

    }
  }
}
.friend__you {
  color: $graymy;
}
.friend__avatar {
  margin-right: 14px;
  float: left;
  position: relative;
  overflow: hidden;
  img {
    width: 44px;
    height: 44px;
    border-radius: 50%;
  }
  &--group {
    border-radius: 50%;
    width: 44px;
    height: 44px;
  }
}
.friend__group-avatar-items {
  width: 50px;
}
.friend__group-avatar-item {
  float: left;
  width: 22px;
  height: 22px;
  margin-right: 1px;
  margin-bottom: 1px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
  &--half {
    height: 44px;
    img {
      height: 44px;
      width: 44px;
    }
  }
}
.friend__meta {
  overflow: hidden;
}
.friend__name {
  color: $fontcolor;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  a {
    color: $fontcolor;
  }
  &--dialog {
    font-size: 14px;
    float: left;
    max-width: 70%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}
.friend__to-text {
  font-size: 12px;
  color: $blue;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.friend__latest-message {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 3px;
  font-size: 13px;
}
.friend__message-time {
  color: $graymy;
  float: right;
  font-size: 13px;
}
.friend__remove {
  background:url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%3E%0A%3Cg%20fill%3D%22none%22%20fill-rule%3D%22evenodd%22%3E%0A%3Crect%20width%3D%2212%22%20height%3D%2212%22%2F%3E%0A%3Cpath%20stroke%3D%22%2393A3BC%22%20stroke-width%3D%221.5%22%20d%3D%22M3%2C3%20L9%2C9%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%20transform%3D%22matrix(-1%200%200%201%2012%200)%22%2F%3E%0A%3Cpath%20stroke%3D%22%2393A3BC%22%20stroke-width%3D%221.5%22%20d%3D%22M3%2C3%20L9%2C9%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%2F%3E%0A%3C%2Fg%3E%0A%3C%2Fsvg%3E");
  width: 12px;
  height: 12px;
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
  opacity: .7;
  padding: 0;
  border: 0;
  appearance: none;
  &:hover {
    opacity: 1;
  }
}
.friend-list--dialogues {
  height: 100%;
  overflow: scroll;  
}