.header-links {
  float: right;
  margin-top: 40px;
  margin-right: 40px;
  font-size: 0;
}
.header-link__item {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 35px;
  margin-left: 20px;
}
.header-link {
  cursor: pointer;
  display: block;
  width: 35px;
  height: 35px;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  border-radius: 50%;
  text-align: center;
  line-height: 33px;
  &:hover {
    .header-link__icon {
      fill: $blue;
    }
  }
}
.header-link__icon {
  vertical-align: middle;
  fill: #1b1b1b;
  &--email {
    width: 17px;
    height: 12px;
  }
  &--notification {
    width: 16px;
    height: 18px;
  }
  &--config {
    width: 20px;
    height: 20px;
  }
}
.header-link__label-count {
  width: 17px;
  height: 17px;
  background-color: $red;
  border-radius: 50%;
  color: #ffffff;
  font-size: 11px;
  text-align: center;
  line-height: 17px;
  top: 4px;
  right: -9px;
  position: absolute;
}
@media (max-width: 768px) {
  .header-links {
    margin-right: 22px;
  }
}
@media (max-width: 760px) {
  .header-links {
    margin-top: 15px;
    margin-right: 0px;
  } 
  .header-link__item {
    margin: 0;
    margin-right: 17px;
    &:last-child {
      margin: 0;
    }
  } 
}