.has-popup {
  position: relative;
}
.popup-dropdown {
  color: $black;
  display: none;
  position: absolute;
  background: #fff;
  border: 1px solid $popup-border-color;
  border-radius: 0 0 2px 2px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .25);
  width: 430px;
  top: 44px;
  right: 0;
  z-index: 100;
  font-size: 14px;
  text-align: left;
  line-height: normal;
  &:before {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border: 1px solid $popup-border-color;
    border-left: 0;
    border-bottom: 0;
    transform: rotate(-45deg);
    background: #ffffff;
    right: 10px;
    top: -7px;
  }
}
.popup-dropdown__header {
  background-color: #fff;
  border-bottom: solid 1px $popup-border-color;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  padding: 8px 12px 6px;
  font-size: 13px;
  font-weight: 600;
}
.popup-dropdown__footer {
  background-color: $magnolia;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  border-top: 1px solid $popup-border-color;
  display: block;
  font-weight: 600;
  padding: 8px 12px;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
}
a.popup-dropdown__footer {
  &:hover {
    text-decoration: none;
  }
}