.notification {
  overflow: hidden;
  padding: 10px 30px 10px 12px;
  border-bottom: 1px solid $popup-border-color;  
  &:last-child {
    border-bottom: 0;
  }
}
.notification__avatar {
  float: left;
  width: 48px;
  margin-right: 15px;
  img {
    width: 48px;
    height: 48px;
    border-radius: 50%;
  }
  .default-photo {
    width: 48px;
    height: 48px;
    svg {
      width: 50%;
      height: 50%;
    }
  }
}
.notification__content {
  overflow: hidden;
  font-size: 13px;
}
.notification__date {
  color: $graymy;
  margin-top: 5px;
}