@media (max-width: 1024px) {
  .menu-open {
    height: 100vh;
    overflow: hidden;
  }
  .sidebar {
    position: absolute;
    display: none;
    z-index: 200;
    height: calc(100vh - 125px);
    overflow: scroll;
    &.sidebar-open {
      display: block;
    }
  }
  .header-sidebar {
    width: $sidebar-opened-width!important;
  }
  .wrapper {
    width: 100%;
  }
}
@media (max-width: 760px) {
  .header {
    background: $blue;
  }
  .logo {
    width: 110px;
  }
  .header, .header-sidebar, .header-sidebar .sidebar-content {
    height: 68px;
  }
  .header-sidebar {
    width: 200px !important;
    border: 0;
  }
  .sidebar {
    &.sidebar-open {
      display: block;
      width: 100%;
      min-height: calc(100vh - 68px);
    }
  }
  .sidebar-content {
    width: auto;
  }
  .footer {
    padding: 0 12px;
    text-align: center;
  }
  .edit-profile-row {
    display: block;
  }
  .edit-profile-col {
    width: auto;
    &--photo {
      width: auto;
      margin: 0 0 50px 0;
    }
    .default-photo {
      margin: 0 auto;
    }
  }
  .settings-form {
    margin-top: 20px;
  }
}