.logout {
  float: right;  
  color: #5f5f5f;
  margin-top: 44px;
  &:hover {
    color: $cyan;
    text-decoration: none;
    .logout__icon {
      fill: $cyan;
    }
  }
}
.logout__icon {
  width: 24px;
  height: 24px;
  fill: #5f5f5f;
  margin-right: 16px;
  vertical-align: middle;
  display: inline-block;
}


@media (max-width: 768px) {
  .logout__text {
    display: none;
  }
  .logout__icon {
    margin-right: 0;
  }
}