.found-friends {
  display: flex;
  flex-wrap: wrap;
}
.found-friend {
  width: 160px;
  margin: 0 20px 30px 0;
  text-align: center;
  .styled-button {
    padding:  5px 0;
    display: block;
    width: 100%;
  }
}
.found-friend__avatar {
  margin-bottom: 10px;
  img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
  }
}
.found-friend__name {
  margin-bottom: 4px;
  font-size: 14px;
  font-weight: 600;
  a {
    color: #3b3a3a;
  }
}
@media (max-width: 767px) {
 .found-friend {
   float: none;
   margin: 0 auto 25px;
   width: 100%;
 } 
}