.empty-dialog-message {
  text-align: center;
}
.empty-dialog-message__icon {
  fill: $graymy;
  width: 100px;
  height: 100px;
}
.dialogue-messages {
  overflow: scroll;
  height: 100%;
  display: none;
  padding: 0 20px 20px;
  flex: 1 0;
}
.dialog-form-box {
  display: none;
  width: 100%;
  background: #f5f6f8;
  padding: 15px 20px; 
}

.message-send-form {
  display: flex;
  align-items: flex-end;
}
.attach-file {
  margin-bottom: 3px;
  position: relative;
  overflow: hidden;
  svg {
    fill: $blue;
  }
  input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }
  label {
    cursor: pointer;
    left: 0;
    margin: 0;
  }
}
.attach-file__icon {
  width: 25px;
  height: 25px;
}
.message-fl {
  flex: 1 0;
  margin: 0 20px;
}
.message-text {
  
}
.message-send {
  padding: 0;
  border: 0;
  border-radius: 0;
  appearance: none;
  margin-bottom: 7px;
  background: none;
  svg {
    fill: $blue;
    padding: 0;
    background: none;
    border: 0;
    width: 25px;
    height: 25px;
  }
}


.create-dialog-box {
  display: none;
  height: 100%;
  flex-direction: column;
}
.new-dialog-users {
  position: relative;
  flex: 1 0;
  overflow: scroll;
}
.new-dialog-user {
  overflow: hidden;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border-bottom: 1px solid #e2e2e2;
  margin: 0;
  .custom-check {
    margin-bottom: 0;
    margin-left: auto;
    line-height: 0;
    font-size: 0;
    label {
      padding-left: 23px;
      &:before {
        background-color: #fff;
      }
    }
  }
  &:hover {
    background: #f3f3f3;
  }
}
.new-dialog-user__avatar {
  width: 35px;
  height: 35px;
  margin-right: 20px;
  img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
  }
}
.new-dialog-user__name {
  font-weight: 500;
}

.create-dialog-box__title {
  position: relative;
  padding: 20px;
  background: $magnolia;
  font-size: 16px;
  border-bottom: 1px solid #e2e2e2;
  .friend__remove {
    display: block;
  }
}
.create-dialog-box__buttons {
  padding: 20px;
  background: $magnolia;
  font-size: 16px;
  border-top: 1px solid #e2e2e2; 
  overflow: hidden;
  .styled-button {
    margin-left: 20px;
  }
}
.close-add-dualog {
  background:url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2212%22%20height%3D%2212%22%20viewBox%3D%220%200%2012%2012%22%3E%0A%3Cg%20fill%3D%22none%22%20fill-rule%3D%22evenodd%22%3E%0A%3Crect%20width%3D%2212%22%20height%3D%2212%22%2F%3E%0A%3Cpath%20stroke%3D%22%2393A3BC%22%20stroke-width%3D%221.5%22%20d%3D%22M3%2C3%20L9%2C9%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%20transform%3D%22matrix(-1%200%200%201%2012%200)%22%2F%3E%0A%3Cpath%20stroke%3D%22%2393A3BC%22%20stroke-width%3D%221.5%22%20d%3D%22M3%2C3%20L9%2C9%22%20stroke-linecap%3D%22round%22%20stroke-linejoin%3D%22round%22%2F%3E%0A%3C%2Fg%3E%0A%3C%2Fsvg%3E") no-repeat;
  background-size: 15px 15px;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  right: 23px;
  opacity: .7;
  padding: 0;
  border: 0;
  appearance: none;
  transform: translateY(-50%);
  &:hover {
    opacity: 1;
  }
}
.dialog-title {
  display: none;
  padding: 20px;
  padding: 15px 20px;
  background: $magnolia;
  width: 100%;
  border-bottom: 1px solid #e2e2e2;
  margin-bottom: 20px;
  overflow: hidden;
}
.dialog-title__users {
  padding-top: 10px;
  float: left;
  font-size: 14px;
  font-weight: 600;
  span {
    font-size: 12px;
    color: $graymy;
    font-weight: normal;
  }
}
.dialogues-list {
  .styled-button {
    
    margin-top: 0;
  }
}
@media (max-width: 767px){
  .dialog-title {
    padding: 10px;
    font-size: 12px;
    span {
      font-size: 10px;
    }
    button {
      font-size: 0;
      padding: 0;
      text-align: center;
      width: 30px;
      height: 30px;
      margin-top: 0;
      &:before {
        content: "+";
        color: $blue;
        display: inline-block;
        font-size: 30px;
        font-weight: 50;
        line-height: 30px;
        margin-left: 6px;
      }
    }
  }
  .dialogue-messages {
    height: calc(100% - 141px);
  }
  .message-send-form {
    .attach-file {order: 2;}
    .message-fl {
      order: 1;
      margin-left: 0;
    }
    .message-send {order: 3;}
  }
  .dialog-form-box {
    padding: 10px;
  }
  .message-send {
    margin-left: 15px;
  }
  .dialog-title__users {
    padding-top: 3px;
  }
  .create-dialog-box__buttons {
    padding: 10px;
    .styled-button {
      margin-top: 0;
    }
  }
}