.text-block {
	img {
		max-width: 100%;
	}
	h2 {
		margin-top: 0;
	}
	ul {
		list-style: none;
		padding-left: 0;
		margin-bottom: 20px;
		& > li {
			padding-left: 16px;
			position: relative;
			&:before {
				content: "";
				left: 0;
				top: 9px;
				width: 6px;
				height: 6px;
				background: $pinkish-red-two;
				position: absolute;
				border-radius: 3px;
			}
		}
		ol, ul {
			margin-left: 20px;
		}
	}
	ol {
		list-style: none;
		padding-left: 0;
		margin-bottom: 20px;
		& > li {
			counter-increment: step-counter;
			&:before {
				content: counter(step-counter) ". ";
				font-weight: bold;
				color: $pinkish-red-two;
			}
		}
		ol, ul {
			margin-left: 20px;
		}
	}

	table {
		width: 100%;
		border-collapse: collapse;
		border: 1px solid #ededed;
		margin-bottom: 20px;
		
		th {
			background: #d6f1fb;
			border: 1px solid #d6f1fb;
			padding: 25px;
			text-align: left;
		}
		td {
			padding: 25px;
			border: 1px solid #ededed;
			border-left: 0;
			border-right: 0;
		}
		tr:hover {
			td {
				background: #f9f8fe;
			}
		}
	}

	p {
		margin: 0 0 20px 0;
	}
}

@media (max-width: 767px) {
	table.responcive-tbl {
		border-bottom: 0;
		thead {
			display: none;
		}
		tbody {
			td {
				padding: 7px 10px;
				display: block;
				border: 0;
				&:before { 
					content: attr(data-th); 
					font-weight: bold;
					display: inline-block;
					width: 50%;  
				}
				&:last-child {
					border-bottom: 1px solid #ededed;
				}
			}
			tr:nth-child(odd) {
				td {
					background: $magnolia;
				}
			}
		}

	}
}
