.current-user {
  margin-top: 40px;
  float: left;
}
.current-user__icon-box {
  width: 45px;
  height: 45px;
  line-height: 48px;
  background-color: #fff;
  border: 1px solid #e1e1e1;
  text-align: center;
  vertical-align: middle;
  border-radius: 50%;
  float: left;
  margin-right: 17px;
}
.current-user__icon {
  width: 18px;
  height: 17px;
  fill: #1b1b1b;
}
.current-user__meta {
  overflow: hidden;
}
.current-user__name {
  font-size: 18px;
}
.current-user__info {
  font-size: 12px;
  color: $graymy;
}

@media (max-width: 768px) {
  .current-user {
    width: 200px;
  } 
  .current-user__name {
    font-size: 14px;
  }
  .current-user__info {
    display: none;
  }
  
}