.main-menu {
  list-style: none;
  margin: 0;
  padding: 30px 0 0 0;
}
.main-menu__item {
  display: block;
  padding: 15px 0 15px 28px;
  position: relative;
  &--active, &--dropdown--open {
    background: #063563;
  }

  &--has-dropdown {
    .main-menu__item-name {
      padding-right: 40px;
    }
    &:after {
      content: "";
      position: absolute;
      width: 5px;
      height: 5px;
      transform: rotate(45deg);
      border: 1px solid #FFF;
      border-left: 0;
      border-top: 0;
      right: 26px;
      top: 22px;
    }
  }
  &--dropdown--open {
    &:after {
      transform: rotate(-135deg);
      top: 25px;
    }
  }
}
.main-menu__link {
  color: #ffffff;
  font-weight: 600;
  display: flex;
  &:hover {
    color: $cyan;
    text-decoration: none;
  }
}
.main-menu__item-name {
  flex: 1 0;
  opacity: 0;
  transition: opacity $sidebar-open-time;
}
.main-menu__icon {
  fill: $cyan;
  width: 18px;
  height: 20px;
  margin-right: 20px;
  
}
.sidebar-open {
 .main-menu__item-name {
   opacity: 1;
 } 
}
.main-menu-dropdown {
  display: none;
  overflow: hidden;
  list-style: none;
  margin: 0;
  padding: 10px 20px 10px 40px;
}
.main-menu-dropdown__item {
  margin-top: 10px;
} 
.main-menu-dropdown__link {
  color: #ffffff;
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  display: inline-block;
  &:hover {
    color: $cyan;
    text-decoration: none;
  }
}