.accordion {

}
.accordion__item-caption {
  padding: 10px 0;
  cursor: pointer;
  font-size: 18px;
  line-height: 25px;
}
.accordion__item-caption--active {
  .faq-accordion-caption {
    border-bottom-color: transparent;
  }  
  & + .accordion__item-container {
    margin-bottom: 20px;
  }
}
.accordion__item-container {
  display: none;
}
.faq-accordion-caption {
  color: $blue;
  border-bottom: 1px dashed $blue;
}