.chat-page {
  display: flex;
  height: calc(100vh - 320px);
  min-height: 330px; 
  border: 1px solid #e2e2e2;
  margin-bottom: 30px;
}
.dialogues-list {
  width: 370px;
  border-right: 1px solid #e2e2e2;
}
.dialogue-open {
  flex: 1 0;
  padding-top: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &--empty {
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 1023px){
  .dialogues-list {
    width: 270px;
  }
}
@media (max-width: 767px){
  
  .dialogue-open {
    display: none;
    width: 100%;
  }
  .dialogues-list {
    width: 100%;
    border-right: 0;
  }
  .chat-page {
    &--dialog-opened {
      .dialogues-list {
        display: none;
      }     
      .dialogue-open {
        display: block;
      }
    }
  }
  .dialogue-messages {
    padding-left: 0;
    padding-right: 0;
  }
  .chat-page {
    /* height: calc(100vh - 260px); */
    height: auto;
  }
  .new-dialog-user {
    padding: 10px;
  }
}