.slider-bt {
  position: absolute;
  z-index: 20;
  width: 48px;
  height: 48px;
  border: 0;
  background: #4d4d4f;
  bottom: -50px;
  &:hover, &:focus {
    outline: 0;
    background: #2e2d2c;
  }
  &:after {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border: 2px solid #FFF;
    border-left: 0;
    border-top: 0;
    left: 50%;
    top: 50%;
  }
}
.slider-bt--prev {
  right: 67px;
  &:after {
    transform: rotate(-135deg) translate(2px, -2px);
  }
}
.slider-bt--next {
  right: 19px; 
  &:after {
    transform: rotate(45deg) translate(-5px, -1px);
  }
}