.rating-item {
  display: flex;  
  padding: 15px 0;
  border-bottom: 1px solid #dedde1;
  justify-content: space-between;
  &:last-child {
    border-bottom: 0;
  }
}
.rating-item__name {
  flex: 1 0;
}
.rating-item__num {
  font-weight: 700;
  width: 43px;
  margin-left: 30px;
}
.rating-item__icon {
  width: 10px;
  height: 10px;
  fill: #ffcb2d;
  margin-right: 7px;
}