.toggle-menu {
  width: 29px;
  height: 19px;
  position: relative;
  border: 0;
  background: none;
  appearance: none;
  border-radius: 0;
  margin: 0 23px;
  &:before, &:after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background: #ffffff;
  }
  &:before {
    top: 0;
    box-shadow: 0 8px 0 0 #FFF;
  } 
  &:after {
    bottom: 0;
  }
  &:focus {
    outline: 0;
  }
}